<template>
  <div>
    <!--  -->
    <div class="ali-Pay">
      <loading v-if="loading"></loading>
      <div class="form" v-html="content"></div>
    </div>
  </div>
</template>

<script>
import Loading from "@/pages/loading.vue";

export default {
  name: "aliPay",
  components: {
    Loading,
  },
  props: {},
  data() {
    return {
      orderId: this.$route.query.orderId,
      content: "",
      loading: true,
    };
  },
  computed: {},
  watch: {},
  created() {},
  mounted() {
    this.paySubmit();
  },
  methods: {
    paySubmit() {
      this.axios
        .post("/pay", {
          orderId: this.orderId, //
          orderName: "Vue", //
          amount: 0.01, //
          payType: 1, //1. 2.
        })
        .then((res) => {
          this.content = res.content;
          setTimeout(() => {
            document.forms[0].submit();
          }, 100);
        });
    },
  },
};
</script>

