<template>
  <div class="orderPay">
    <div class="content">
      <div class="order">
        <div class="order_pay">
          <!--  -->
          <div class="item_order">
            <div class="item_icon"></div>
            <div class="item_Tips">
              <h1>! </h1>
              <p>
                <span class="points">30</span>, 
              </p>
              <p>{{ addressInfo }}</p>
            </div>
          </div>
          <!--  -->
          <div class="payment_Details">
            <p></p>
            <p>
              <span>{{ payment }}</span
              >
            </p>
            <p>
              
              <span :class="iconStyle" @click="iconClick(iconStyle)"></span>
            </p>
          </div>
        </div>
        <!--  -->
        <div :class="Isproduct_name">
          <ul>
            <li>
              <p></p>
              <p>{{ orderId }}</p>
            </li>
            <li>
              <p></p>
              <p>
                {{ addressInfo }}
              </p>
            </li>
            <li>
              <p>
                :
              </p>
            </li>
            <li v-for="(item, index) in orderDetail" :key="index">
              <div class="product_name">
                <img v-lazy="item.productImage" />
                <p>{{ item.productName }}</p>
              </div>
            </li>
            <li>
              <p></p>
              <p> </p>
            </li>
          </ul>
        </div>
      </div>
      <!--  -->
      <div class="pay_method">
        <h3></h3>
        <p></p>
        <!--  -->
        <div class="pay-ali" @click="payType(1)"></div>
        <!--  -->
        <div class="pay-wechat" @click="payType(2)"></div>
      </div>
    </div>
    <!--  -->
    <el-dialog
      title=""
      :visible.sync="weiXinDialog"
      width="30%"
      @close="closeWeixinPay()"
    >
      <img :src="payImg" />
      <p></p>
      <p></p>
      <span slot="footer" class="dialog-footer"> </span>
    </el-dialog>
    <!--  -->
    <el-dialog title="" :visible.sync="PaymentDailog" width="30%">
      <span></span>
      <span slot="footer" class="dialog-footer">
        <el-button class="btnPay" @click="goOrder()"></el-button>
        <el-button class="info" @click="Unpaid()"></el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import QRCode from "qrcode";
export default {
  name: "orderPay",
  components: {},
  props: {},
  data() {
    return {
      iconStyle: "down", //,
      Isproduct_name: "product_informationDisplay",
      orderId: this.$route.query.orderNo, //
      addressInfo: "", //
      orderDetail: [], //,
      weiXinDialog: false, //
      payImg: "",
      PaymentDailog: false,
      T: "", //id
      payment: 0, //
    };
  },
  computed: {},
  watch: {},
  created() {},
  mounted() {
    this.getOrderDetail();
  },
  methods: {
    //icon
    iconClick(val) {
      if (val == "down") {
        this.iconStyle = "downUp";
        this.Isproduct_name = "product_information";
      } else {
        this.iconStyle = "down";
        this.Isproduct_name = "product_informationDisplay";
      }
    },
    getOrderDetail() {
      this.axios.get(`/orders/${this.orderId}`).then((res) => {
        let item = res.shippingVo;
        this.addressInfo = `${item.receiverName} ${item.receiverMobile} ${item.receiverProvince} ${item.receiverCity} ${item.receiverDistrict} ${item.receiverAddress}`;
        this.orderDetail = res.orderItemVoList;
        this.payment = res.payment;
      });
    },
    payType(val) {
      if (val == 1) {
        //
        window.open("/#/order/aliPay?orderId=" + this.orderId, "_blank");
      } else {
        this.weiXinDialog = true;
        this.axios
          .post("/pay", {
            orderId: this.orderId, //
            orderName: "Vue", //
            amount: 0.01, //
            payType: 2, //1. 2.
          })
          .then((res) => {
            QRCode.toDataURL(res.content)
              .then((url) => {
                this.payImg = url;
                this.loopOrderState();
              })
              .catch(() => {
                this.$message.error(",");
              });
          });
      }
    },
    closeWeixinPay() {
      this.PaymentDailog = true;
      clearInterval(this.T);
    },
    Unpaid() {
      this.PaymentDailog = false;
    },
    goOrder() {
      this.$router.push("/order/list");
    },
    //
    loopOrderState() {
      this.T = setInterval(() => {
        this.axios.get(`/orders/${this.orderId}`).then((res) => {
          if (res.status == 20) {
            clearInterval(this.T);
            this.goOrder();
          }
        });
      }, 1000);
    },
  },
};
</script>

